<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>
        <!---->
        <div class="search">
          <el-input
            placeholder=""
            v-model="username"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="layer.chicken"
          :append-to-body="true"
        >
          <el-table :data="chicken">
            <el-table-column
              property="pid"
              label=""
              width="150"
            ></el-table-column>
            <el-table-column
              property="title"
              label=""
              width="200"
            ></el-table-column>
            <el-table-column
              property="update_time"
              label=""
            ></el-table-column>
          </el-table>
        </el-dialog>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="layer.egg"
          :append-to-body="true"
        >
          <el-table :data="egg">
            <el-table-column
              property="remark"
              label=""
              width="150"
            ></el-table-column>
            <el-table-column
              property="title"
              label=""
              width="200"
            ></el-table-column>
            <el-table-column
              property="number"
              label=""
              width="100"
            ></el-table-column>
            <el-table-column
              property="update_time"
              label=""
            ></el-table-column>
          </el-table>
        </el-dialog>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="layer.forage"
          :append-to-body="true"
        >
          <el-table :data="forage">
            <el-table-column
              property="id"
              label="ID"
              width="100"
            ></el-table-column>
            <el-table-column
              property="remark"
              label=""
            ></el-table-column>
            <el-table-column
              property="create_time"
              label=""
            ></el-table-column>
          </el-table>
        </el-dialog>

        <!---->
        <div class="create">
          <span>: {{ total }} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="80"> </el-table-column>
            <el-table-column prop="pid_name" label="" width="150">
            </el-table-column>
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <el-avatar
                  style="width:30px;height:30px"
                  :src="scope.row.avatar"
                ></el-avatar>
              </template>
            </el-table-column>
            <el-table-column prop="username" label=""> </el-table-column>
            <el-table-column prop="phone" label="" width="150">
            </el-table-column>
            <el-table-column prop="chicken_number" width="100" label="">
              <template slot-scope="scope">
                <span
                  v-if="scope.row.chicken_number"
                  @click="show('chicken', scope.row.id)"
                  class="brand"
                  style="cursor: pointer;"
                  v-text="scope.row.chicken_number"
                ></span>
                <span class="info" v-else>0</span>
              </template>
            </el-table-column>
            <el-table-column prop="egg_number" width="100" label="">
              <template slot-scope="scope">
                <span
                  v-if="scope.row.egg_number"
                  @click="show('egg', scope.row.id)"
                  class="danger"
                  style="cursor: pointer;"
                  v-text="scope.row.egg_number"
                ></span>
                <span class="info" v-else>0</span>
              </template>
            </el-table-column>
            <el-table-column prop="sign_number" width="100" label="">
              <template slot-scope="scope">
                <span
                  v-if="scope.row.sign_number"
                  @click="show('forage', scope.row.id)"
                  class="success"
                  style="cursor: pointer;"
                  v-text="scope.row.sign_number"
                ></span>
                <span class="info" v-else>0</span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" width="120" label="">
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          background
          :page-size="pageSize"
          layout="prev, pager, next"
          @current-change="change"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'

export default {
  name: 'index',
  data() {
    return {
      username: '',
      page: 1,
      userid: 0,
      pageSize: 20,
      total: 0,
      layer: { chicken: false, egg: false, forage: false },
      data: [],
      chicken: [],
      egg: [],
      forage: [],
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let data = await request(
        `/user?page=${this.page}&username=${this.username}`,
        ''
      )
      if (data.error_code === 10000) {
        this.data = data.data.data.map((item) => {
          item.create_time = item.create_time.substring(0, 10)
          return item
        })
        this.total = data.data.total
      }
    },
    async _getChicken() {
      let res = await request(`chickenlog?userid=${this.userid}`)
      if (res.error_code === 10000) {
        this.chicken = res.data.data
      }
    },
    async _getEgg() {
      let res = await request(`egglog?userid=${this.userid}`)
      if (res.error_code === 10000) {
        this.egg = res.data.data
      }
    },
    async _getForage() {
      let res = await request(`foragelog?userid=${this.userid}`)
      if (res.error_code === 10000) {
        this.forage = res.data.data
      }
    },
    change(e) {
      this.page = e
      this._request()
    },
    search() {
      if (!this.username.trim().length) {
        this.$message.error('')
        return false
      }
      this._request()
    },
    show(param, userid) {
      this.userid = userid
      this.layer[param] = true
      if (param === 'chicken') {
        this._getChicken()
      } else if (param === 'egg') {
        this._getEgg()
      } else {
        this._getForage()
      }
    },
    refresh() {
      this.page = 1
      this.title = ''
      this._request()
    },
  },
}
</script>

<style scoped></style>
